import moment from "moment";
import { createContext, useContext } from "react";

const LS_KEY = "alive-app";

export interface IAppState {
    name: string;
    number: string;
    birthdate: string;
    photo: string;
    logo: string;
    validFrom: string;
}

const AppStateContext = createContext<IAppState | undefined>(undefined);

export function AppStateProvider({
    children,
    value,
}: {
    children: React.ReactNode;
    value: IAppState;
}) {
    return (
        <AppStateContext.Provider value={value}>
            {children}
        </AppStateContext.Provider>
    );
}

export function useAppState() {
    const context = useContext(AppStateContext);
    if (!context) {
        throw new Error("useAppState must be used within an AppStateProvider");
    }

    // Use this december if it is january to october, otherwise use next december
    const validUntil =
        moment().month() < 11
            ? moment().endOf("year")
            : moment().add(1, "year").endOf("year");

    // Increase valid from so it is not older than 6 years
    const validFrom = moment(context.validFrom);
    while (moment().diff(validFrom, "years") >= 6) {
        validFrom.add(1, "year");
    }

    // Increment the birthdate if the age is 25 or more so the user is still 24
    let birthdate = moment(context.birthdate);
    while (moment().diff(birthdate, "years") >= 25) {
        birthdate = birthdate.add(1, "year");
    }

    return {
        ...context,
        save: (value: IAppState) => {
            context.name = value.name;
            context.number = value.number;
            context.birthdate = value.birthdate;
            context.photo = value.photo;
            context.logo = value.logo;
            context.validFrom = value.validFrom;

            // Save the state
            localStorage.setItem(LS_KEY, JSON.stringify(context));
        },
        validUntil,
        tweakedBirthdate: birthdate.toISOString(),
        tweakedValidFrom: validFrom.toISOString(),
    };
}

export function loadAppState(): IAppState {
    const state = localStorage.getItem(LS_KEY);
    if (state) {
        return JSON.parse(state);
    }
    return {
        name: "John Doe",
        number: "S420123456789L",
        birthdate: "1990-01-01",
        validFrom: moment().startOf("year").toISOString(),
        photo: "data:image/jpeg;base64,",
        logo: "data:image/png;base64,",
    };
}
